    explanation:
      'DECLARE: Als erstes wird der CURSOR im Deklarationsteil des PL/SQL-Blocksdefiniert, das heißt, er bekommt einen Namen und eine SELECT-Anweisung, diebeliebig komplex sein kann. Eine INTO-Klausel ist weder erlaubt noch notwendig.Damit verbunden ist intern das Anlegen eines benannten Zwischenspeicherbereichsfür die Aufnahme der Ergebnismenge. Alle weiteren Schritte werden imAusführungsteil durchgeführt.  \nOPEN: Beim Öffnen eines CURSOR wird die zugehörige SELECT-Anfrage an das Datenbankmanagementsystem geschickt und dort ausgewertet. Von dort wird die Ergebnismenge in den benannten Speicherbereich geladen, was heißt, dass während der gesamten Zeit der CURSOR-Verarbeitung die Ergebnisdatenmenge unverändert bleibt. Nach der OPEN-Anweisung parallel von anderen Transaktionen durchgeführte Manipulationen haben keinen Einfluss mehr auf die Ergebnisdaten, die ja bereits als Kopie im Zwischenspeicherbereich vorliegen. \nFETCH: In PL/SQL ist ein Zeiger (CURSOR) nach dem Öffnen sofort auf den ersten Datensatz dieser Ergebnismenge positioniert, so dass mit einer FETCH-Anweisung dieser Satz aus dem benannten Speicherbereich in die lokalen PL/SQL-Variablen hinein ausgelesen werden kann. Als zusätzliche Funktion positioniert die FETCH-Anweisung den Zeiger auch noch auf den nächsten Datensatz im Zwischenspeicher. \nCLOSE: War der letzte FETCH nicht erfolgreich, d.h., sind die lokalen Variablen leer, dann heißt dass, dass alle Datensätze abgearbeitet sind und die Schleife verlassen werden kann. Beim Schließen des CURSOR wird der damit verbundene Zwischenspeicherbereich wieder freigegeben.',
