    id: 2007,
    category: 4,
    difficulty: 3,
    text: 'Welche Regeln gehören zu den zwölf Basisregeln, die E.F. Codd für relationale Datenbanksysteme aufstellte?',
    explanation:
      'E. F. Codd, einer der Begründer der relationalen Datenbanken, beschreibt\nzwölf Basisregeln, die eine relationale Datenbank auszeichnen,\nsich aber auch auf andere Datenbanktypen verallgemeinern lassen\n\nRegel 1: Informationsregel\nJede Information einer relationalen Datenbank wird in genau einer Weise durch Werte in\nRelationen dargestellt und damit redundanzfrei und einheitlich verwaltet. \n\nRegel 2: Garantierter Zugriff\nJedes einzelne Feld der Datenbank ist durch eine Kombination von Relationsname,\nPrimärschlüssel und Spaltenname erreichbar.\n\nRegel 3: Systematische Behandlung fehlender Information\nIn einer relationalen Datenbank müssen Spalten mit fehlender Information (Nullwerte)\neinheitlich darstellbar sein. Diese Nullwerte werden systematisch als fehlende\nInformation von den Standardwerten (z.B. Strings mit Leerzeichen) unterschieden.\nSpalten können auch so eingerichtet werden, dass Nullwerte nicht erlaubt sind.\n\nRegel 4: Dynamischer Online-Katalog (Data Dictionary)\nEin Datenbankschema wird in derselben Weise wie die gespeicherten Daten selbst\nbeschrieben ¿ nämlich in Tabellen, dem sogenannten Data Dictionary. Autorisierte\nBenutzer können das Data Dictionary in gleicher Weise abfragen wie die eigentliche\nDatenbasis.\n\nRegel 5: Allumfassende Sprache\nEin Datenbanksystem muss eine Sprache unterstützen, die allumfassend sein, d.h. sie \nerfüllt die folgenden Aufgaben:\nDefinition der Benutzerdaten,\nDefinition von Sichten als virtuelle Tabellen,\nManipulation von Benutzerdaten,\nÜberprüfung von Integritätsregeln,\nVergabe von Benutzerrechten und Autorisierung,\nTransaktionskontrolle und Transaktionshandling \n\nRegel 6: Benutzersichten und Datenänderungen\nFür unterschiedliche Benutzergruppen und Anwendungen sind unterschiedliche\nSichten (Views) auf die Datenbank notwendig. In einfachen Views, z.B. Teilansichten\neiner Tabelle, sollen auch Datenänderungen möglich sein.\n\nRegel 7: HIGH-LEVEL INSERT, UPDATE und DELETE\nIn der Datenbank muss das Einfügen, Ändern und Löschen von Daten möglich sein.\nDabei soll das System sich den optimalen Zugriffspfad zur schnellen Durchführung\nder Transaktion selbst suchen.\n\nRegel 8: Physische Datenunabhängigkeit\nAnwendungsprogramme und Anwenderoberflächen bleiben unverändert, wenn Veränderungen\nan der Speicherstruktur oder der Zugriffsmethode in der Datenbank vorgenommen werden.\n\nRegel 9: Logische Datenunabhängigkeit\nAnwendungsprogramme und Anwenderoberflächen bleiben unverändert, wenn sich\nBasisrelationen verändern, die nicht direkt die Anwendungsprogramme betreffen. \n\nRegel 10: Integritätsunabhängigkeit\nIntegritätsbedingungen, die von der Datenbank erfüllt werden müssen, werden mithilfe\nder relationalen Datenbanksprache definiert, im Data Dictionary abgelegt und\nvom DBMS ausgeführt. Diese Integritätsbedingungen gehören nicht ins Anwenderprogramm.\n\nRegel 11: Verteilungsunabhängigkeit\nEine relationale Datenbank besitzt die Verteilungsunabhängigkeit. Das heißt, die Anwendungsprogramme\nbleiben unverändert, wenn die verteilte Datenhaltung auf mehreren\nRechnern eingeführt oder wieder zurückgenommen wird und mehrere Datenbanken zu\neiner Datenbank zusammengelegt werden.\n\nRegel 12: Unterwanderungsverbot\nFalls das DBMS eine andere 3GL-Sprache wie C oder Java zulässt, darf diese Sprache\nnicht die aufgestellten Regeln 1 bis 11 ver',
