    text: 'Welche Aussagen hinsichtlich Normalisierung und Normalformen sind wahr?',
    explanation:
      '1. Die Normalisierung geht von einem Datenvolumen aus, bei dem die in den Relationen enthaltenen Daten schon vorhanden sind und sich nicht verändern. Auf diesen Relationen werden aufgrund funktionaler Abhängigkeiten Maßnahmen zur besseren Strukturierung durchgeführt. In der Praxis sind natürlich die Relationen beim Datenbankentwurf noch nicht in allen Fällen mit Daten gefüllt; Bewegungsdaten verändern sich immer im laufenden Betrieb. Der Entwickler muss also einen\ngewissen Spürsinn haben, wo in Zukunft unerwünschte funktionale Abhängigkeiten und Redundanzen auftreten könnten. Außerdem wird beim konzeptionellen Modell als ER-Modell oft schon intuitiv die dritte Normalform gewählt. \n2. Anders sieht das Ganze bei der Reorganisation alter Datenbestände aus. In diesen Fällen ist die Normalformenlehre ein nützliches Instrument, um Redundanzen zu beseitigen und eine gute Datenstruktur zu erreichen. \n3. Durch die Normalisierung ergibt sich bei größeren Projekten eine Vielzahl von mit Fremdschlüsselbeziehungen verbundenen Tabellen. Informationen, die inhaltlich im Sinne der Objektorientierung zusammengehören, werden unter Umständen auf viele Tabellen verteilt. Bei Abfragen kann es zu erheblichen Performanceverlusten kommen, da oft viele Relationen aufwändig miteinander verknüpft werden müssen. \n4. Aus den angeführten Gründen ist eine Verletzung der dritten Normalform (Denormalisierung) in der Praxis manchmal gewollt. Die Normalisierung ist nicht als Dogma zu verstehen, aber nach wie vor ein analytisches Instrument, um Relationenstrukturen auf ihre Qualität hin zu überprüfen. Mit Datenbanktriggern wird das Problem der Redundanzen handhabbar.',
