    difficulty: 3,
    text: 'Welche SQL-Befehle lösen eine IMMEDIATE-Integritätsprüfung aus? ',
    explanation:
      'Ziel der Integritätsprüfung sind ja semantisch korrekte Daten, d.h. Daten, die allen definierten Constraints genügen. Sind Constraints definiert und aktiviert, dann sind in der Datenbasis nur solche Daten persistent gespeichert, die die Constraints erfüllen, so dass man mit 100%-iger Sicherheit sagen kann, alle Daten sind korrekt. Problematische Aktionen in diesem Zusammenhang sind dann alle Datenmanipulationen, also INSERT, UPDATE, DELETE. Anfragen (SELECTs) sind in diesem Zusammenhang unkritische Aktionen, die keine Prüfung erfordern - es soll ja nichts gespeichert werden, nur angezeigt. \n\nDie drei DML-Befehle INSERT, UPDATE, DELETE lösen also IMMEDIATE-Integritätsprüfung aus. \nCOMMIT beendet eine Transaktion und löst damit eine DEFERRED-Integritätsprüfung aus. \nROLLBACK beendet zwar auch eine Transaktion, jedoch indem sie rückgängig gemacht wird. Eine Integritätsprüfung ist daher unnötig und wird nicht ausgeführt. \nDDL-Anweisungen (CREATE, ALTER, DROP) beinhalten ein implizites COMMIT, was dafür sorgt, dass jede DDL-Anweisung unmittelbar in die DICTIONARY-Tabellen eingetragen wird. Dieses implizite COMMIT hat zur Folge, dass DDL-Anweisungen immer auch eine Transaktion beenden und eine DEFERRED-Integritätsprüfung auslösen.  \nSAVE, CHECK sind keine SQL-Befehle. Es gibt wohl das CHECK-Constraint, aber eine Prüfung wird damit nicht ausgelöst. \n',
