    explanation:
      'Schon 1975 verabschiedete ANSI/SPARC6 einen Standard, nach dem sich die Architektur von Datenbanken richtet. Das Architekturmodell unterscheidet drei Abstraktionsebenen oder Sichten:\n\nExterne Ebene \nDie externe Ebene enthält die Benutzersichten auf die Daten bzw. den Ausschnitt aus den Gesamtdaten, den spezielle Benutzergruppen benötigen. Diese Ebene wird vom Datenbankentwickler verwaltet.\n\nKonzeptionelle Ebene\nDiese Ebene hat die logische Darstellung der Gesamtsicht der Daten in einem speziellen Datenmodell, z.B. dem relationalen Datenmodell zum Inhalt. Man differenziert hier zwischen dem konzeptionellen Schema, wie dem ER-Modell, welches noch unabhängig vom konkret eingesetzten Datenmodell ist, und dem logischen Datenbankschema, welches schon auf das Modell eines bestimmten Datenbanktyps angepasst ist. Diese Ebene wird primär vom Datenbankentwickler verwaltet.\n\nInterne Ebene\nInformation über die Art und den Aufbau der Datenstrukturen auf dem physikalischen Speicher und Zugriffsmechanismen sind Bestandteil der internen Ebene. Diese Ebene wird vom Datenbankadministrator verwaltet.',
